module.exports = {
  data: function ()
  {
    return {
      vModelValue: this.value
    }
  },
  props: ['value'],
  watch: {
    value: function (val)
    {
      this.vModelValue = val
    },
    vModelValue: function (val)
    {
      this.$emit('input', val)
    }
  },
  //vue2.0Virtual DOM Checkboxbug
  beforeUpdate: function ()
  {
    this.vModelValue = this.value
  }
}


