<template>
    <div class="code-wrapper">
        <codemirror v-model="code"
                    :options="options"
                    :hint="true"
                    ref="codemirror"
                    @changed="codeChange">
        </codemirror>
    </div>
</template>

<script type="text/ecmascript-6">
  let vm = {}
  export default {
    name: 'trick-codemirror',
    created () {
      vm = this;
    },
    components: {
      codemirror: (resolve) =>
      {
        require(['vue-codemirror/codemirror.vue'], (component) =>
        {
          resolve(component);
          vm.$emit("loaded")
        })
      }
    },
    props: {
      mode: {
        type: String,
        default: 'text/html'
      },
      theme: {
        type: String,
        default: ''
      },
      placehold: {
        type: String,
        default: ''
      }
    },
    data(){
      return {
        code: '',
        loading: false,
        options: {
          // Codemirror
          tabSize: 2,
          lineNumbers: true,
          line: true,
          // sublimeemacsvim
          keyMap: "sublime",
          // Ctrlautocompleteautocompletehint
          extraKeys: {"Ctrl": "autocomplete"},
          // 
          foldGutter: true,
          gutters: ["CodeMirror-linenumbers", "CodeMirror-foldgutter"],
          // 
          styleSelectedText: true,
          highlightSelectionMatches: {showToken: /\w/, annotateScrollbar: true},
          // more codemirror config...
          // hint
        }
      }
    },
    methods: {
      codeChange(newCode){
        this.$emit("change", newCode)
      }
    },
    watch: {
      placehold(newCode){
        this.code = newCode
      }
    },
    mounted(){
      this.code          = this.placehold
      this.options.mode  = this.mode
      this.options.theme = this.theme
      this.$emit("change", this.code)
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
    .code-wrapper {
        flex: 1;
        & + .code-wrapper {
            border-left: 2px solid #eee;
        }
    }

    .CodeMirror {
        height: 100% !important;
        width: 100%;
    }
</style>





